import React from 'react';
import { useGallery } from '../../config/GalleryContext';

import classes from './Gallery.module.css';

export default function Gallery() {

    const { hits, isLoading } = useGallery()

    return (
        <section>
            <div className={classes.gallery}>
                {(!isLoading && hits.length > 0) &&
                    hits.map((hit, index) =>
                        <div>
                            <img
                                src={hit.largeImageURL}
                                alt={hit.largeImageURL}
                            />
                            <div className={classes['user-overlay']}>
                                <div className={classes.user}>
                                    <img src={hit.userImageURL} alt={hit.userImageURL} />
                                    <p>{hit.user}</p>
                                </div>
                                <a href={hit.largeImageURL}>Preview</a>
                            </div>
                        </div>
                    )
                }
            </div>
            {(!isLoading && hits.length === 0) &&
                <div className={classes.error}>
                    <h1>No results Found</h1>
                </div>
            }
        </section>
    )
}

